<template>
    <v-container>
    <top class="top"></top>
    <v-layout class="mid" row="">
        <v-flex xs2>
         <v-layout column>
              <v-btn color="light-blue lighten-5" block ripple=""  align="center" v-bind:key=item.genre v-for="item in toolbar()" @click="changeroute(item.url)"><b>{{ item.genre }}</b></v-btn>
      </v-layout>
        </v-flex>
        <v-flex offset-xs1 xs8>
            <br>
            <v-card>
            <pdf scr=article.context></pdf>
            </v-card>
        </v-flex>
        <v-flex xs2>
          <right></right>
        </v-flex>
    </v-layout>
    <bottom></bottom>
  </v-container>
</template>

<script>
import api from '../../store/api'
import pdf from 'vue-pdf'

export default {
    props:['id'],
    data(){
        return{
            article:null
        }
    },
    methods: {
        toolbar : function (){
            console.log(this.article)
            if(this.article.classID==4 )
            {
                return [
                    {genre:"",url:"construction"},
                    {genre:"",url:"manufacture"},
                    {genre:"",url:"electricity"}]
            }
            if(this.article.classID==5 || this.article.classID==6)
            {
                return [
                    {genre:"",url:"lastest"},
                    {genre:"E-DM",url:"DM"},
                    {genre:"",url:"new"}]
            }
            if(this.article.classID==7)
            {
                return [
                    {genre:"",url:"instruction"},
                    {genre:"FAQ",url:"FAQ"},
                    {genre:"",url:"note"},
                    {genre:"",url:"theory"},
                    {genre:"",url:"skill"},
                    {genre:"/",url:"conversion"},
                    {genre:"/",url:"data"},
                    {genre:"",url:"t_link"},
                    {genre:"",url:"issue"}]
            }
            if(this.article.classID==8)
            {
                return [
                    {genre:"/",url:"equipment"},
                    {genre:"",url:"film"},
                    {genre:"",url:"picture"}]
            }
            if(this.article.classID==9)
            {
                return [
                    {genre:"",url:"relation"},
                    {genre:"",url:"friend"},
                    {genre:"",url:"national"},
                    {genre:"",url:"develop"},
                    {genre:"",url:"governoment"},
                    {genre:"",url:"source"},
                    {genre:"",url:"commerce"},
                    {genre:"/",url:"work"},
                    {genre:"",url:"rest"},
                    {genre:"",url:"life"},
                    {genre:"",url:"enterprise"},
                    {genre:"",url:"association"}]
            }
        },
         changeroute (url) {
            this.$router.push('/home/'+ url)
            document.body.scrollTop = 0
        document.documentElement.scrollTop = 0
        }
    },
    beforeMount(){
        let self = this;
        api.getArticle(this.id).then(res=>{
            self.article = res.data.article
        }).catch(error=>{
            alert(error)
        })
    }
}
</script>

<style>
.top {
    position: relative;
    z-index: 100;
}

.mid {
    position: relative;
    z-index: 0;
}
</style>



